@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  background: #c0c0c0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.nav--btn {
  @apply m-3 p-3 rounded-md text-gray-400 hover:text-white hover:bg-gray-700 focus:outline-none focus:ring-2 focus:ring-inset focus:ring-white;
}
.active {
  @apply bg-white text-gray-700;
}

.momorandum--div {
  @apply p-3 my-10 mx-auto max-w-screen-sm font-medium text-gray-900 bg-white border border-gray-200 rounded-lg dark:bg-gray-700 dark:border-gray-600 dark:text-white;
}
.momorandum--ol {
  @apply list-decimal;
}
.momorandum--li {
  @apply p-2 ml-10;
}
.momorandum--h1 {
  @apply m-2 text-xl font-bold;
}

.love--graphy--div {
  @apply max-w-screen-sm p-5 mb-8 bg-gray-50 rounded-lg border border-gray-100 dark:bg-gray-800 dark:border-gray-700;
}
